@charset "UTF-8";


/*spiner*/
.loader {
  width: 50px;
  height: 50px;
  border-radius: 50%;
  margin: 3em;
  display: inline-block;
  position: relative;
  vertical-align: middle;
}
.loader,
.loader:before,
.loader:after {
  animation: 1s infinite ease-in-out;
}
.loader:before,
.loader:after {
  width: 100%;
  height: 100%;
  border-radius: 50%;
  position: absolute;
  top: 0;
  left: 0;
}
.loader-9,
.loader-10 {
  animation: loader9 0.4s infinite linear;
}
.loader-10 {
  animation: loader10 60s infinite ease-in-out;
}
.loader-9:before,
.loader-10:before {
  content: '';
  width: 80%;
  height: 80%;
  top: 10%;
  left: 10%;
  box-shadow: 5px -3px 0 rgba($principal,0.7),
  5px 5px 0 rgba($contraste,0.7),
  -3px 5px 0 rgba($blanc_menu,0.7),
  -5px -5px 0 rgba($principal,0.7);
}
.loader-9:after,
.loader-10:after {
  content: '';

  z-index: 2;
  top: -3px;
  left: -3px;
}

@keyframes loader9 {
  0%   { transform: rotate(0deg); }
  100% { transform: rotate(360deg); }
}

@keyframes loader10 {
  0%   { transform: rotate(0deg); }
  100% { transform: rotate(360000deg); }
}

